                                      active-value="1"
                                      inactive-value="0">
                                    </el-switch>
                                </div>
                          </div>
                         
                      </li>
                      <li>
                         <span class="layoutspan"> : </span>&nbsp;
                                <div class="layoutBox" style="width:436px">
                                   <el-input  v-model="paramsForamData.reagent"></el-input>
                                </div>
                      </li>
                    </ul>
                </el-form>
             </div>
       </div>
       <div slot="footer" class="dialog-footer">
          <el-button type="noticon" @click="saveParamsForm('paramsForm')"> </el-button>
          <el-button type="noticonsecondBtn" @click="cancelParams('paramsForm')"> </el-button>
       </div>
      </el-dialog>
      <el-dialog :visible.sync="visiDialogImg"  width="1000px" :title="vTitle">
                 <div style="height:400px">
                      <p>:</p>
                      <el-upload
                          ref="outUpload"
                          :file-list="fileList"
                          :on-change="fileChange"
                          action="/"
                          list-type="picture-card"
                          :auto-upload="false"
                          :on-preview="handlePictureCardPreview"
                          :http-request="uploadFile"
                          :on-remove="handleRemove">
                          <i class="el-icon-plus"></i>
                      </el-upload>
                 </div>
                 <div slot="footer" class="dialog-footer">
                    <el-button type="noticon" @click="uploadImg"> </el-button>
                    <el-button type="noticonsecondBtn" > </el-button>
                 </div>
      </el-dialog>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="">
      </el-dialog>
      <el-dialog :visible.sync="dialogVisibleMap" width="1000px" title="">
          <div style="height:500px;position:relative">
             <div style="position:absolute;top:12px;left:12px;z-index:9999">
                <el-input v-model="lng" style="width:100px"></el-input>
                 <el-input  v-model="lat" style="width:100px"></el-input>
             </div>
             <map-Comp @getLntLat="getLntLat"></map-Comp>
          </div>
          <div slot="footer" class="dialog-footer">
                    <el-button type="noticon" @click="saveLngLat"> </el-button>
                    <el-button type="noticonsecondBtn" @click="dialogVisibleMap = false"> </el-button>
          </div>
      </el-dialog>
  </div>
</template>

<script>
import mapComp from './map';
import {exportTableData} from '../../assets/js/tableExport'

export default {
  name: "",

  data() {
    return {
      lng:'',//
      lat:'',//
      dialogVisibleMap:false,
      frequencyOption:[{
        name:'S',
      },{
        name:'M',
      },{
        name:'H',
      }],
      loading:true,
      dialogImageUrl: "",
      dialogVisible: false,
      visiDialog: false,
      treeConfig: {
        showCheckbox: false,
        defaultId: 6
      },
      treedata: [],
      outfileListPre:[],
      insidefileListPre:[],
      buildfileListPre:[],
      outfileListP:[],
      insidefileListP:[],
      buildfileListP:[],
      fileList: [],//
      file:[],//
      insidefileList: [],//
      insidefile:[],//
      buildfileList: [],//
      buildfile:[],//
      visiDialogImg:false,
      imgType:'',//
      vTitle:'',
      tableData: [],//
      stationFormData: {
        stationName: "", // 
        stationCode: "", // 
        stationPassworld: "", //
        provinceId: "", //
        areaId: "", //
        localManagementUnits: "", //
        basinId: "", //
        stationRiver: "", //
        stationType: "", //
        surfaceWaterTypes: "", //
        targetLevel: "", //
        stationClassify: "", //
        longitude: "", // 
        latitude: "", // 
        sort: "", //
        stationProvincesUpDown: "", //
        enabled: "", //10
        enabledTime: "", //
        city: "", //
        stationProperty: "", //
        stationFunction: "", //
        operCompanyCode:'',//
        operCompanyName:'',
        stationAddress: "", //
        stationBriefInfo: "", //
        peripheralHistory: "", //
        stationId:'',//ID
        freUnit:'',//
        freNumber:'',
        dataFrequency:'',
        projectCodes:'',//
        projectCodesArr:[],
        
      }, //
      
      stationFormRules: {
        stationName: [
          { required: true, message: "", trigger: "blur" }
        ],
        freNumber: [
          { required: true, message: "", trigger: "blur" }
        ],
        stationCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        provinceId: [
          { required: true, message: "", trigger: "change" }
        ],
        projectCodesArr: [
          { required: true, message: "", trigger: "change" }
        ],
        areaId: [
          { required: true, message: "", trigger: "change" }
        ],
        basinId: [
          { required: true, message: "", trigger: "change" }
        ],
        stationRiver: [
          { required: true, message: "", trigger: "blur" }
        ],
        surfaceWaterTypes: [
          { required: true, message: "", trigger: "change" }
        ],
        freUnit:[ { required: true, message: "", trigger: "change" }],
        targetLevel: [
          { required: true, message: "", trigger: "change" }
        ],
        longitude: [{ required: true, message: "", trigger: "blur" }],
        latitude: [{ required: true, message: "", trigger: "blur" }],
        sort: [{ required: true, message: "", trigger: "blur" }]
      }, //
      paramsForamData:{
        projectCode:'',//Code
        manufactor:'',//
        equipmentModel:'',//
        equipmentName:'',//
        analyticalMethods:'',//
        equipmentOrange:'',//
        detectionLimit:'',//
        upperLimit:'',//
	      lowerLimit:'',//
        manufactureCode:'',//
        createTime:'',//
        isenable:'0',//
        reagent:'',//
        id:'',//Id
      },//
      paramsFormRules:{
         projectCode: [
          { required: true, message: "", trigger: "change" }
        ],
        manufactor: [
          { required: true, message: "", trigger: "blur" }
        ],
        equipmentModel: [
          { required: true, message: "", trigger: "blur" }
        ],
      },
      provinceData: [], //
      areaData: [], //
      basinData: [], //
      stationTypeData: [], //
      surfaceWaterData: [], //
      targetLevelData: [], //
      stationClassData: [], //
      stationFunData: [], //
      operCompanyData:[],//
      factorData:[],//
      equipPlaceholder:"",
      stationMn:'',//MN
      paramsId:'',//Id
      currentPage: 1, //
      pageSize: 10, //
      total: 0, //
      pageList: [10, 20, 30], //
      fieldList:["PROJECT_NAME","EQUIPMENT_NAME","MANUFACTOR","ANALY_METHONS","REAGENT","DETECTION_LIMIT","UPPER_LIMIT","LOWER_LIMIT","PROJECT_UNIT","MANUFACTURE_CODE","CREATE_TIME","ISENABLE"],
    };
  },
  created() {
    this.init();
    this.editorInit();
   
  },
  inject: ["globelDelet"],
  methods: {
    //
    getLntLat(n){
      this.lng=n.lng.toFixed(2);
      this.lat=n.lat.toFixed(2);
    },
    //
    saveLngLat(){
      this.stationFormData.longitude= this.lng; // 
      this.stationFormData.latitude= this.lat;// 
      this.dialogVisibleMap=false;
    },
    //
    paramsDelet(id){
      this.globelDelet("/SiteManagement/equipment/delete",id,this.getParams);
    },
    //
    allparamsDelet(){
      this.globelDelet("/SiteManagement/equipment/delete",this.paramsId,this.getParams);
       
    },
    //
    exportedForm(){
        
      let {table} = this.$refs;
      let tableBox = table.$el;
      let header = tableBox.querySelector('.el-table__header');
      let title = '';
      let params = {
        stationCodes:this.stationMn,
        pattern:'yyyy-MM-dd HH:mm:ss',
        headerProp:this.fieldList.join(','),
      
      };
     
      exportTableData(header,title,'','1',"/SiteManagement/exports/equipmentExcel",params);
    },
    tableChange(val){
      this.paramsId = "";
      let arr = val;
    
      for (let i in arr) {
        this.paramsId += arr[i].EQUIPMENT_ID + ",";
      }
      this.paramsId = this.paramsId.substring(
        0,
        this.paramsId.length - 1
      );
    
    },
    init() {
      //
      this.$http.get("/SiteManagement/area/provinceInfo").then(res => {
        if (res.data.code == 0) {
          this.provinceData = res.data.content.dataList;
        }
      });
      //
      let params = {
        flag: 0
      };
      this.$http.post("/SiteManagement/basinn/query").then(res => {
        if (res.data.code == 0) {
          this.basinData = res.data.content.dataList;
        }
      });
      //
      this.$http
        .get("/SiteManagement/station/querySurfaceWaterTypes")
        .then(res => {
          if (res.data.code == 0) {
            this.surfaceWaterData = res.data.content.dataList;
          }
        });
      //
      this.$http.post("/SiteManagement/stationType/query").then(res => {
        if (res.data.code == 0) {
          this.stationTypeData = res.data.content.dataList;
        }
      });
       //
      this.$http.post("/SiteManagement/stationClassify/queryAll").then(res => {
        if (res.data.code == 0) {
          this.stationClassData = res.data.content.dataList;
        }
      });
      //
      this.$http.get("/SiteManagement/station/queryTargetLevel").then(res => {
        if (res.data.code == 0) {
          this.targetLevelData = res.data.content.dataList;
        }
      });
      //
      this.$http.get("/SiteManagement/station/queryFunction").then(res => {
        if (res.data.code == 0) {
          this.stationFunData = res.data.content.dataList;
        }
      });
      //
      this.$http.get("/SiteManagement/operCompany/queryAll").then(res => {
        if (res.data.code == 0) {
          this.operCompanyData = res.data.content.dataList;
        }
      });
      //
      this.$http.post("/SiteManagement/index/query").then(res => {
        if (res.data.code == 0) {
          this.factorData = res.data.content.dataList;
          this.factorData.unshift({
            projectName:'',
            projectCode:'all'
          })
        }
      });
    },
    //
    provinceChange() {
      this.$http
        .get("/SiteManagement/area/areaInfo", {
          params: {
            name: this.stationFormData.provinceId
          }
        })
        .then(res => {
          if (res.data.code == 0) {
            this.areaData = res.data.content.dataList;
            this.stationFormData.city = "";
            this.stationFormData.areaId = "";
          }
        });
    },
    //
    areaFocus() {
      this.$refs["stationForm"].validateField("provinceId", () => {});
    },
    //
    editorInit() {
      this.loading=false;
      if (this.$route.query.code == 1) {
       
        //
        this.$http
          .get("/SiteManagement/area/areaInfo", {
            params: {
              name: this.$store.state.stationMessage.provinceId.areaId
            }
          })
          .then(res => {
            if (res.data.code == 0) {
              this.areaData = res.data.content.dataList;
              this.stationMn=this.$store.state.stationMessage.stationMn;
              this.getImages();
              this.getParams();
              //
              this.stationFormData = {
                stationId:this.$store.state.stationMessage.stationId,
                stationName: this.$store.state.stationMessage.stationName, // 
                stationCode: this.$store.state.stationMessage.stationMn, // 
                stationPassworld: this.$store.state.stationMessage.stationPassworld, //
                provinceId:'',//
                areaId:'',//
                localManagementUnits: this.$store.state.stationMessage.localMangermnetUnits, //
                basinId:'',//
                stationRiver: this.$store.state.stationMessage.stationRiver, //
                stationType:'',//
                surfaceWaterTypes: this.$store.state.stationMessage.suifaceWaterTypes, //
                targetLevel: this.$store.state.stationMessage.targerWaterQuality, //
                stationClassify: "", //
                longitude: this.$store.state.stationMessage.longitude, // 
                latitude: this.$store.state.stationMessage.latitude, // 
                sort: this.$store.state.stationMessage.sort, //
                stationProvincesUpDown: this.$store.state.stationMessage.stationProvincesUpDown, //
                enabled: this.$store.state.stationMessage.enabled+'', //10
                enabledTime: new Date(this.$store.state.stationMessage.enableTime).format('yyyy-MM-dd hh:mm:ss'), //
                city:'',//
                operCompanyCode:this.$store.state.stationMessage.operCompanyId,//
                stationProperty: this.$store.state.stationMessage.stationProperty, //
                stationFunction: this.$store.state.stationMessage.stationFunction, //
                stationAddress: this.$store.state.stationMessage.stationAddress, //
                stationBriefInfo: this.$store.state.stationMessage.stationBriefInfo, //
                peripheralHistory: this.$store.state.stationMessage.peripheralHistory,//
                dataFrequency:'',
                freUnit:'',//
                freNumber:'',
                projectCodesArr:[],
              };
              if(this.$store.state.stationMessage.dataFrequency){
                let str=this.$store.state.stationMessage.dataFrequency;
                this.stationFormData.freNumber=str.substr(0,1);
                this.stationFormData.freUnit=str.substr(str.length-1,1);
              }
             
              if (this.$store.state.stationMessage.provinceId) {
                this.stationFormData.provinceId = this.$store.state.stationMessage.provinceId.areaId; //
              }
              if (this.$store.state.stationMessage.area) {
                this.stationFormData.areaId = this.$store.state.stationMessage.area.areaId; //
              }
              if (this.$store.state.stationMessage.basinn) {
                this.stationFormData.basinId = this.$store.state.stationMessage.basinn.basinnId; //
              }
              if (this.$store.state.stationMessage.stationType) {
                this.stationFormData.stationType = this.$store.state.stationMessage.stationType.stationTypeId; //
              }
             
              if (this.$store.state.stationMessage.cityId) {
                this.stationFormData.city = this.$store.state.stationMessage.cityId.areaId; //
              }
              if (this.$store.state.stationMessage.stationClassify) {
                this.stationFormData.stationClassify = this.$store.state.stationMessage.stationClassify.stationClassifyId; //
              }
              if(this.$store.state.stationMessage.projects && this.$store.state.stationMessage.projects.length>0){
                let fArr = this.$store.state.stationMessage.projects;
                this.stationFormData.projectCodesArr=[]
                for(let i in fArr){
                   this.stationFormData.projectCodesArr.push(fArr[i].projectCode)
                }
              
                if(fArr.length == this.factorData.length-1){
                    this.stationFormData.projectCodesArr.unshift('all')
                }
              }
            }
           
          });
      }
    },
    selectChange(val){
     
      for(let i in this.factorData){
        if(this.factorData[i].projectCode == val){
            this.equipPlaceholder= this.factorData[i].projectName +"+"
        }
      }
        
    },
    //
    saveParamsForm(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
           
             if(this.stationMn == ""){
                this.$message({
                  message:'',
                  type: 'warning',
                  duration:1500,
                });
             }else{
                  let params={};
                  params.projectCode=this.paramsForamData.projectCode;
                  params.manufactor=this.paramsForamData.manufactor;
                  params.equipmentModel=this.paramsForamData.equipmentModel;
                  params.equipmentName=this.paramsForamData.equipmentName;
                  params.analyticalMethods=this.paramsForamData.analyticalMethods;
                  params.equipmentOrange=this.paramsForamData.equipmentOrange;
                  params.detectionLimit=this.paramsForamData.detectionLimit;
                  params.upperLimit=this.paramsForamData.upperLimit;
                  params.lowerLimit=this.paramsForamData.lowerLimit;
                  params.manufactureCode=this.paramsForamData.manufactureCode;
                  params.createTime=this.paramsForamData.createTime;
                  params.isenable=this.paramsForamData.isenable;
                  params.reagent=this.paramsForamData.reagent;
                  params.id=this.paramsForamData.id
                  params.stationMn=this.stationMn;
                  this.$http.post("/SiteManagement/equipment/addOrEdit",params).then(res=>{
                         if(res.data.code == 0){
                                this.$message({
                                  message:res.data.message,
                                  type: 'success',
                                  duration:1500,
                                });
                                this.visiDialog=false;
                               
                                this.cancelParams(formName);
                                this.getParams();
                         }
                  })
             }
          } else {
           
            return false;
          }
        });
    },
    //
    editorParams(obj){
      this.visiDialog=true;
       this. paramsForamData={
        projectCode:obj.PROJECT_CODE,//Code
        manufactor:obj.MANUFACTOR,//
        equipmentModel:obj.EQUIPMENT_MODEL,//
        equipmentName:obj.EQUIPMENT_NAME,//
        analyticalMethods:obj.ANALY_METHONS,//
        equipmentOrange:obj.EQUIPMENT_ORANGE,//
        detectionLimit:obj.DETECTION_LIMIT,//
        upperLimit:obj.UPPER_LIMIT,//
	      lowerLimit:obj.LOWER_LIMIT,//
        manufactureCode:obj.MANUFACTURE_CODE,//
        createTime:new Date(obj.CREATE_TIME).format('yyyy-MM-dd hh:mm:ss'),//
        isenable:obj.ISENABLE,//
        reagent:obj.REAGENT,//
        id:obj.ID,//Id
      }
    },
    factorChange(v){
       let arr=[];
      
       if(v == 'all'){
         
         for(let i=0;i<this.factorData.length;i++){
          
            arr.push(this.factorData[i].projectCode)
         }
         this.stationFormData.projectCodesArr=arr
       }
      
    },
    //
    addParams(){
        this.visiDialog=true;
         this.paramsForamData.id='';
    },
    //
    cancelParams(formName){
      this.visiDialog=false;
      this.resetForm(formName);
   
        this.paramsForamData.projectCode='';//Code
        this.paramsForamData.manufactor='';//
        this.paramsForamData.equipmentModel='';//
        this.paramsForamData.equipmentName='';//
        this.paramsForamData.analyticalMethods='';//
        this.paramsForamData.equipmentOrange='';//
        this.paramsForamData.detectionLimit='';//
        this.paramsForamData.upperLimit='';//
	      this.paramsForamData.lowerLimit='';//
        this.paramsForamData.manufactureCode='';//
        this.paramsForamData.createTime='';//
        this.paramsForamData.isenable='0';//
        this.paramsForamData.reagent='';//
        
     
    },
    //
    resetForm(formName) {
        this.$refs[formName].resetFields();
    },
    bl(activeName, oldActiveName) {
      // console.log(activeName)
      //  if(activeName == 1 || activeName == 2){
      //    return false;
      //  }
    },
    uploadFile(files) {
      
      
    },
    upload(name) {
      this.visiDialogImg=true;
      this.imgType=name;
      
      //
     
      if(name == 'out'){
        this.vTitle='';
       
      }else if(name == 'inside'){
         this.vTitle='';
      }else if(name == 'build'){
         this.vTitle='';
       
      }
     
    },//
    uploadImg(){
      let formData = new FormData();
      formData.append('mn',this.stationMn);
      for(let i in this.file){
          formData.append('files',this.file[i].raw)
      }
      formData.append('type',this.imgType);
      let config = {
            headers: {
              "Content-Type": "multipart/form-data"
            }
      };
      this.$http.post("/SiteManagement/station/addImage", formData, config).then(res=>{
        this.file=[];
        this.fileList=[]
          this.visiDialogImg=false;
          this.getImages();
      })
    },//
    //
    getImages(){
        this.$http
            .get("/SiteManagement/station/queryImage", {
              params:{
                mn:this.stationMn
              }
            })
            .then(res=>{
                let list = res.data.content.dataList;
                this.outfileListPre=[];
                this.insidefileListPre=[];
                this.buildfileListPre=[];
                for(let i =0;i<list.length;i++){
                 
                  if(list[i].type == 'OUT_IMAGE'){
                     
                     for(let j=0;j< list[i].path.length;j++){
                        this.outfileListPre.push({
                          name:list[i].type,
                          url:'http://120.77.41.22:61007/SiteManagement-Service/image/'+list[i].path[j]
                        });
                        this.outfileListP.push(
                        'http://120.77.41.22:61007/SiteManagement-Service/image/'+list[i].path[j]
                        )
                     }
                  }else if(list[i].type == 'INSIDE_IMAGE'){
                     for(let j=0;j< list[i].path.length;j++){
                        this.insidefileListPre.push({
                          name:list[i].type,
                          url:'http://120.77.41.22:61007/SiteManagement-Service/image/'+list[i].path[j]
                        });
                         this.insidefileListP.push(
                        'http://120.77.41.22:61007/SiteManagement-Service/image/'+list[i].path[j]
                        )
                     }
                  }else if(list[i].type == 'BUILD_IMAGE'){
                     for(let j=0;j< list[i].path.length;j++){
                        this.buildfileListPre.push({
                          name:list[i].type,
                          url:'http://120.77.41.22:61007/SiteManagement-Service/image/'+list[i].path[j]
                        });
                          this.buildfileListP.push(
                        'http://120.77.41.22:61007/SiteManagement-Service/image/'+list[i].path[j]
                        )
                     }
                  }
                };
            })
    },
    //
    getParams(){
      this.loading=true;
        this.$http.get("/SiteManagement/equipment/queryInstrumentParameters",{
          params:{
            stationCodes:this.stationMn,
            pageNumber:this.currentPage,
            pageSize:this.pageSize,
          }
        }).then(res=>{
           if(res.data.code == 0){
               this.tableData=res.data.content.dataList;
               this.total=res.data.content.totalRecords;
               this.loading=false;
           }
        })
    },
    //
    fileChange(file, fileList){
      this.file = fileList;
    },
    //
    insidefileChange(file, fileList){
      this.insidefile = fileList;
    },
    //
    buildfileChange(file, fileList){
      this.buildfile = fileList;
    },

    handleRemove(file, fileList) {
     
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },

    indexMethod(index) {
      return index + 1;
    },
    search(callback) {},
    closess(done) {
      done();
    },
    //
    submitSites(formName) {
     this.stationFormData.projectCodes=this.stationFormData.projectCodesArr.join(',')
        
        
       
      this.$refs[formName].validate(valid => {
        if (valid) {
         
          for(let i in this.operCompanyData){
            if(this.operCompanyData[i].operCompanyId == this.stationFormData.operCompanyCode){
                this.stationFormData.operCompanyName=this.operCompanyData[i].operCompanyName
            }
          };
          this.stationFormData.dataFrequency=this.stationFormData.freNumber+this.stationFormData.freUnit;
          let  str = this.stationFormData.projectCodesArr;
         if(str.indexOf('all') >0){
            str.splice(str.indexOf('all'),1);
         }
          this.stationFormData.projectCodes=str.join(',');
          this.$http.post("/SiteManagement/station/editOrAdd", this.stationFormData).then(res => {
               this.$message({
                  message:res.data.message,
                  type: 'success',
                  duration:1500,
                });
               this.stationMn=res.data.content.info[0].stationMn;
               this.stationFormData.stationId=res.data.content.info[0].stationId;
          });
          
        } else {
          return false;  
        }
      });
    },
    //
    additionalSites() {},
    checkedNode(val) {}
  },
  computed: {
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    },
    totalPageNumber() {
      //
      let number = 0;
      number = Math.ceil(this.total / this.pageSize);
      return number;
    }
  },

  watch: {
    stationInfo: function(newValue, oldValue) {
      // if(newValue[0].label != oldValue[0].label){
      //    console.log(newValue[0].label)
      // }
      // console.log(newValue[0].label);
    }
  },
  components:{
    mapComp
  }
};
</script>

<style scoped lang="less">
.historypage {
  height: 100%;
}

.edui-editor {
  background: #fff;
}
.baseinfo {
  padding: 12px;
  width: 1020px;
  margin: 0 auto;
  li {
    margin-bottom: 20px;
    .layoutspan {
      width: 120px;
      i {
        color: red;
        margin-right: 4px;
      }
    }
    .sousuo {
      cursor: pointer;
      display: inline-block;
      width: 34px;
      height: 30px;
      vertical-align: -2px;
      line-height: 30px;
      text-align: center;
    }
  }
}
.stationManage {
  .uploadImg {
    li {
      padding-left: 20px;
      margin-bottom: 12px;
      p {
        height: 32px;
        font-size: 16px;
        position: relative;
        &:before {
          content: "";
          width: 3px;
          height: 10px;
          background: #166bce;
          position: absolute;
          left: -10px;
          top: 5px;
        }
      }
    }
  }
}
.uploadBtnBox {
  margin-bottom: 32px;
  padding: 8px 0px 0px 8px;
  > span {
    color: rgba(153, 153, 153, 1);
  }
}
.layoutspan{
  width: 80px;
}
</style>



